<template>
    <div id="inStocks">
        <!--  -->
        <el-breadcrumb separator="/" style="padding-left:10px;padding-bottom:10px;font-size:12px;">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <!--  -->
        <el-card>
            <!--  -->
            <el-form size="small" :inline="true" :model="queryMap" class="demo-form-inline">
                <el-form-item label="">
                    <el-select  clearable  v-model="queryMap.type" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                        <el-option label="" value="3"></el-option>
                        <el-option label="" value="4"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">

                    <el-input
                            clearable
                            @clear="search"
                            v-model="queryMap.inNum"
                            placeholder=""
                            @keyup.enter.native="search"
                            class="input-with-select"
                    >
                    </el-input>
                </el-form-item>
                <el-form-item>
                    <el-select    v-model="queryMap.status" placeholder="">
                        <el-option label="" :value="0"></el-option>
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-date-picker
                            :clearable="false"
                            v-model="range"
                            type="datetimerange"
                            :value-format="'yyyy-MM-dd HH:mm:ss'"
                            :picker-options="pickerOptions"
                            range-separator=""
                            start-placeholder=""
                            end-placeholder=""
                            align="right">
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button icon="el-icon-refresh" type="warning" @click="clearTime"></el-button>
                </el-form-item>
                <el-form-item>
                    <router-link to="/business/product/add-stocks">
                        <el-button type="success" icon="el-icon-plus"></el-button>
                    </router-link>
                </el-form-item>

                <el-form-item>
                    <el-button type="button" size="small"
                               v-hasPermission="'inStock:export'"
                               icon="el-icon-download"
                               @click="downExcel"></el-button>
                </el-form-item>

            </el-form>
            <!--  -->
            <el-table size="mini" v-loading="loading" :data="tableData" border style="width: 100%;" height="420">
                <el-table-column label="#" prop="id" width="50"></el-table-column>
                <el-table-column  prop="inNum" :show-overflow-tooltip='true' label="" width="180"></el-table-column>
                <el-table-column prop="type" label="" width="100">
                    <template slot-scope="scope">
                        <el-tag  type="success" v-if="scope.row.type===1"></el-tag>
                        <el-tag  v-else-if="scope.row.type===2"></el-tag>
                        <el-tag  type="danger" v-else-if="scope.row.type===3"></el-tag>
                        <el-tag  type="warning" v-else></el-tag>
                    </template>
                </el-table-column>

                <el-table-column prop="productNumber" label="" sortable width="100"></el-table-column>
                <el-table-column prop="phone" label="" width="150"></el-table-column>
                <el-table-column prop="status" label="" width="100">
                    <template slot-scope="scope">
                        <el-tag size="mini" type="danger" effect="plain" v-if="scope.row.status==1"></el-tag>
                        <el-tag size="mini" effect="plain" v-if="scope.row.status==0"></el-tag>
                        <el-tag size="mini" effect="plain" type="warning" v-if="scope.row.status==2"></el-tag>
                    </template>
                </el-table-column>

                <el-table-column prop="operator" label="" width="180"></el-table-column>
                <el-table-column prop="supplierName" label="" width="180"></el-table-column>
                <el-table-column prop="createTime" label="" sortable width="180"></el-table-column>
                <el-table-column label="" fixed="right" width="200">
                    <template slot-scope="scope">
                        <el-button icon="el-icon-view"  @click="detail(scope.row.id)"
                                   type="text" size="small">

                        </el-button>

                        <!--                        -->
                        <span v-if="scope.row.status==0">
                             <el-popconfirm
                                     @onConfirm="remove(scope.row.id)"
                                     style="margin-left: 20px;"
                                     confirmButtonText=''
                                     cancelButtonText=''
                                     icon="el-icon-info"
                                     iconColor="red"
                                     title=""
                             >
              <el-button type="text" slot="reference" size="mini" icon="el-icon-s-operation"></el-button>
            </el-popconfirm>
                        </span>
                        <!--   ()-->
                        <span v-if="scope.row.status===1">
                             <el-popconfirm
                                     @onConfirm="back(scope.row.id)"
                                     style="margin-left:10px;"
                                     confirmButtonText=''
                                     cancelButtonText=''
                                     icon="el-icon-info"
                                     iconColor="green"
                                     title=""
                             >
                            <el-button type="text" slot="reference" size="mini" icon="el-icon-s-operation"></el-button>
                        </el-popconfirm>
                                <el-popconfirm
                                        style="margin-left:20px;"
                                        @onConfirm="del(scope.row.id)"
                                        title=""
                                >
                            <el-button type="text" slot="reference" size="small" icon="el-icon-delete"></el-button>
                        </el-popconfirm>
                        </span>

                        <!--                        -->
                        <span v-if="scope.row.status==2">
                          <el-popconfirm
                                  style="margin-left:20px;"
                                  @onConfirm="publish(scope.row.id)"
                                  title=","
                          >
                        <el-button type="text" slot="reference" size="small" icon="el-icon-refresh"></el-button>
                    </el-popconfirm>
                        <el-popconfirm
                                style="margin-left:20px;"
                                @onConfirm="del(scope.row.id)"
                                title=""
                        >
                            <el-button type="text" slot="reference" size="small" icon="el-icon-delete"></el-button>
                        </el-popconfirm>
                        </span>

                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination
                    style="margin-top:10px;"
                    background
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="queryMap.pageNum"
                    :page-sizes="[10, 20, 30, 40]"
                    :page-size="queryMap.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
            ></el-pagination>
            <!--  -->
            <el-dialog title="" :visible.sync="dialogVisible" @close="closeDetail" width="60%">
                <!--                -->
                <el-card class="box-card" style="margin-bottom: 10px;">
                    <div  class="text item">
                        <el-row :gutter="20">
                            <el-col :span="6">
                                <div class="grid-content bg-purple">
                                    <span style="font-size: 11px;color: #303030;">:</span> &nbsp;<el-tag size="mini" >{{supplier.address}}</el-tag>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div class="grid-content bg-purple">
                                    <span style="font-size: 11px;color: #303030;">:</span> &nbsp;<el-tag size="mini" >{{supplier.name}}</el-tag>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div class="grid-content bg-purple">
                                    <span style="font-size: 11px;color: #303030;"> :</span> &nbsp;<el-tag size="mini"  >{{supplier.contact}}</el-tag>
                                </div>
                            </el-col>
                            <el-col :span="6">
                                <div class="grid-content bg-purple">
                                    <span style="font-size: 11px;color: #303030;"> : </span>&nbsp;<el-tag size="mini" >{{supplier.phone}}</el-tag>
                                </div>
                            </el-col>

                        </el-row>
                    </div>
                </el-card>

                <!--                -->
                <el-steps simple v-if="pStatus==0"  style="margin-left: 10px;margin-bottom: 5px;" :space="200" :active="3" finish-status="success">
                    <el-step title="" ></el-step>
                    <el-step title=""></el-step>
                    <el-step title=""></el-step>
                </el-steps>
                <el-steps simple v-if="pStatus==2"  style="margin-left: 10px;margin-bottom: 5px;" :space="200" :active="2" finish-status="success">
                    <el-step title="" ></el-step>
                    <el-step title=""></el-step>
                    <el-step title=""></el-step>
                </el-steps>
                <span>
          <template>
            <el-table height="260" max-height="350" border :data="detailTable" style="width: 100%">
              <el-table-column prop="name" label=""></el-table-column>
              <el-table-column :show-overflow-tooltip="true" prop="pnum" label=""></el-table-column>
               <el-table-column prop="model" label=""></el-table-column>
              <el-table-column
                      prop="imageUrl"
                      label=""
                      align="center"
                      width="150px"
                      padding="0px"
              >
                <template slot-scope="scope">
                  <img
                          :src="'https://www.zykhome.club/'+scope.row.imageUrl"
                          alt
                          style="width: 30px;height:30px"
                  />
                </template>
              </el-table-column>
               <el-table-column prop="count" label=""></el-table-column>
                <el-table-column prop="unit" label=""></el-table-column>
            </el-table>
              <!--              -->
        <el-pagination
                style="margin-top:20px;"
                background
                @current-change="handleDetailSizeChange"
                :current-page="this.pageNum"
                :page-size="3"
                layout="prev, pager, next,total"
                :total="this.detailTotal">
        </el-pagination>
          </template>

        </span>
            </el-dialog>
        </el-card>
    </div>
</template>

<script>
    import axios from "axios";

    export default {
        data() {
            return {
                pickerOptions: {
                    shortcuts: [
                        {
                            text: '()',
                            onClick(picker) {
                                const end = new Date();
                                const start = new Date(new Date(new Date().toLocaleDateString()).getTime());
                                picker.$emit('pick', [start, end]);
                            }
                        },
                        {
                            text: '',
                            onClick(picker) {
                                const end = new Date(new Date(new Date().toLocaleDateString()).getTime());
                                const start = new Date(new Date(new Date().toLocaleDateString()).getTime());
                                start.setTime(start.getTime() - 3600 * 1000 * 24);
                                picker.$emit('pick', [start, end]);
                            }
                        },
                        {
                            text: '',
                            onClick(picker) {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                                picker.$emit('pick', [start, end]);
                            }
                        }, {
                            text: '',
                            onClick(picker) {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                                picker.$emit('pick', [start, end]);
                            }
                        }, {
                        }, {
                            text: '',
                            onClick(picker) {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 60);
                                picker.$emit('pick', [start, end]);
                            }
                        }, {
                            text: '',
                            onClick(picker) {
                                const end = new Date();
                                const start = new Date();
                                start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                                picker.$emit('pick', [start, end]);
                            }
                        }]
                },
                supplier:{},
                detailTotal:0,
                pageNum:1,
                detailId:undefined,
                loading: true,
                detailTable: [],
                dialogVisible: false,
                total: 0,
                queryMap: {pageNum: 1, pageSize: 10, status: 0},
                tableData: [],
                range:[],
                pStatus:'',//flag
            };
        },
        methods: {

            downExcel() {
                const $this = this;
                const res = axios.request({
                    url: "business/inStock/excel",
                    method: "post",
                    responseType: "blob"
                })
                    .then(res => {
                        if(res.headers['content-type']==='application/json'){
                            return $this.$message.error("Subject does not have permission [inStock:export]");
                        }
                        const data = res.data;
                        let url = window.URL.createObjectURL(data); // url
                        const a = document.createElement("a");
                        document.body.appendChild(a);
                        a.href = url;
                        a.download = ".xls";
                        a.click();
                        window.URL.revokeObjectURL(url);
                    });
            },
            /**
             * 
             */
            closeDetail(){
                this.pageNum=1;
            },
            clearTime(){
                this.queryMap= {pageNum: 1, pageSize: 10, status: 0};
                this.queryMap.startTime=null;
                this.queryMap.endTime=null;
                this.range=[];
            },
            /**
             *  
             */
            handleDetailSizeChange(newSize) {
                this.pageNum = newSize;
                this.detail(this.detailId);
            },

            /**
             *
             */
            async publish(id){
                const { data: res } = await this.$http.put("business/inStock/publish/"+id);
                if (!res.success) {
                    return this.$message.error(":"+res.data.errorMsg);
                } else {
                    await this.loadTableData();
                    return this.$message.success("");
                }
            },
            /**
             * 
             */
            async back(id){
                const { data: res } = await this.$http.put("business/inStock/back/"+id);
                if (!res.success) {
                    return this.$message.error(":"+res.data.errorMsg);
                } else {
                    await this.loadTableData();
                    return this.$message.success("");
                }
            },
            /**
             * 
             */
            async remove(id) {
                const {data: res} = await this.$http.put("business/inStock/remove/" + id);
                if (!res.success) {
                    return this.$message.error(":" + res.data.errorMsg);
                } else {
                    await this.loadTableData();
                    return this.$message.success("");
                }
            },
            /**
             */
            async del(id) {
                const {data: res} = await this.$http.get("business/inStock/delete/" + id);
                if (!res.success) {
                    return this.$message.error(":" + res.data.errorMsg);
                } else {
                    await this.loadTableData();
                    return this.$message.success("");
                }
            },
            /**
             * 
             */
            async detail(id) {
                this.detailId=id;
                const {data: res} = await this.$http.get("business/inStock/detail/" + id+"?pageNum="+this.pageNum);
                if (!res.success) {
                    this.$message.error(":" + res.data.errorMsg);
                } else {
                    this.detailTable = res.data.itemVOS;
                    this.detailTotal = res.data.total;
                    this.supplier=res.data.supplierVO;
                    this.pStatus=res.data.status;
                    this.dialogVisible = true;

                }

            },
            /**
             * 
             */
            async loadTableData() {

                if(this.range!=null&&this.range.length===1){
                    this.queryMap.startTime=this.range[0];
                }else if(this.range!=null&&this.range.length===2){
                    this.queryMap.startTime=this.range[0];
                    this.queryMap.endTime=this.range[1];
                }

                const {data: res} = await this.$http.get("business/inStock/findInStockList", {
                    params: this.queryMap
                });
                if (!res.success) {
                    return this.$message.error(":"+res.data.errorMsg);
                } else {
                    this.total = res.data.total;
                    this.tableData = res.data.rows;
                }
            },
            /**
             * 
             */
            handleSizeChange(newSize) {
                this.queryMap.pageSize = newSize;
                this.loadTableData();
            },
            /**
             * 
             */
            search() {
                this.queryMap.pageNum = 1;
                this.loadTableData();
            },
            /**
             * 
             */
            handleCurrentChange(current) {
                this.queryMap.pageNum = current;
                this.loadTableData();
            }
        },
        created() {
            this.loadTableData();
            setTimeout(() => {
                this.loading = false;
            }, 1000);
        }
    };
</script>


